const PageHeader = () => {
    
    const text = "Hello!";
    let i = -5;  //To give the bliker some time before the text appear

    //Creating the typing effect on page load
    window.onload = function() {
        if (i < text.length) {
            document.querySelector("#typing__effect").innerHTML += text.charAt(i);
            i++;
            setTimeout(window.onload, 250);
        }
    }

    //Debouncing method to stop vhGen function from running at every resize
    const debounce = (fn) => {
        let timeoutId;
        return function() {
            if (timeoutId) {
                clearTimeout(timeoutId);  //Clearing the timeout if it's already set
            }
    
            timeoutId = setTimeout(() => {
                fn();
            }, 100);
        }
    } 

    //To get the exact vh in mobile devices
    const vhGen = () => {
        let vh = window.innerHeight * 0.01;
        document.documentElement.style.setProperty('--vh', `${vh}px`);
    }

    vhGen();

    //Running the function on resizing
    window.addEventListener('resize', debounce(() => vhGen()) );
    
    return(
        <main className="maincontent">
            <div className="maincontent--headertxt">
                <h1 id="typing__effect">
                    <img src="./images/ufo.png" alt="UFO" className="maincontent--ufo"/>
                </h1>
                <span className="headertxt--lineblinker"></span>
            </div>
            <h2>I'm Pabodha</h2>

        </main>

    );
}

export default PageHeader;